import Vue from 'vue'
import Router from 'vue-router'
 
import Login from '@/components/auth/login'
import Main from '@/components/common/main'
import Register from '@/components/auth/register'
// user 
import userList from '@/components/manage/list'
import Add from '@/components/manage/add'

// 
import proList from '@/components/product/list'
import addPro from '@/components/product/addPro'
// import upload from '@/components/product/upload'
// 
import sellerList from '@/components/seller/list' 






import { getStore } from '@/config/storage'
import store from '@/store/index'


Vue.use(Router)

const router = new Router({
  mode:'history',
  base:__dirname,
  scrollBehavior (to, from, savedPosition) { //
      if (savedPosition) {
        return savedPosition
      } else {
        return { x: 0, y: 0 }
      }
  },
  routes: [
   
    { path: '/', redirect: '/login' ,hidden:true},
    {
      path: '/regin',
      name: '',
      component: Register,
      hidden:true   
    },
    {
      path: '/login',
      name: '',
      component: Login,
      hidden:true
    },
    {
      path: '/user',
      name: '',
      iconCls:'el-icon-message',
      component: Main,
      meta: {
        requireAuth:true //
      },
       children:[
          {path:'userlist',component:userList,name:'',meta:{page:1}},
          {path:'add',component:Add,name:'',meta:{page:2}}
          
      ]
    },
    {
      path: '/user',
      name: '',
      component: Main,
      iconCls:'el-icon-share ',
      meta: {
        requireAuth:true //
      },
       children:[
          {path:'proList',iconCls:'el-icon-share ',component:proList,name:'',meta:{page:3}},
           {path:'addPro',iconCls:'el-icon-share ',component:addPro,name:'',meta:{page:4}},
           {path:'upload',iconCls:'el-icon-share ',
           component:resolve => require(['@/components/product/upload'],resolve),//
           name:'',meta:{page:5}}
      ]
    },
   {
      path: '/user',
      name: '',
      iconCls:'el-icon-upload',
      component: Main,
      meta: { 
        requireAuth:true //
      },
       children:[
          {path:'sellerlist',iconCls:'el-icon-upload',component:sellerList,name:'',meta:{page:6}}
      ]
     }
  ]
  
})
// main
if(getStore('userInfo')){
  console.log(getStore('userInfo'))
  store.commit('REMEBER_USER', getStore('userInfo'))
    
}

router.beforeEach((to,from,next) => {
  if(to.meta.requireAuth) {
  
    if(store.state.user.username){
      next()
    }else {
      alert("")
      next({
        path: '/login',
        query: {redirect: to.fullPath}
      })
    }
  }else {
     next()
  }
})
//afterEach
router.afterEach(( to,from ) => {
   let titles = []
   let matched = to.name
  // title
  document.title = matched
  const decode = decodeURIComponent
  // 
  if(process.env.NODE_ENV === 'development'){
      console.info(`[] ${decode(from.path || '')} => ${decode(to.path)}`)
  }
  
})


export default router


