<<template>
        <div class="form_box">
             <img src="../../assets/logo.png">
             <el-form :model="user" :rules="rules2" ref="ruleForm" label-position="center" class="demo-ruleForm login-container" >
                    <el-form-item prop="username">
                        <el-input placeholder = "" auto-complete="on" v-model="user.username">
                            <template slot="prepend">                                <i class="el-icon-edit"> </i>
                            </template>    
                        </el-input>
                    </el-form-item>
                    <el-form-item prop="name">
                        <el-input placeholder = "" auto-complete="off"  v-model="user.name">
                            <template slot="prepend">
                                <i class="el-icon-edit">  </i>
                            </template>    
                        </el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input placeholder="" type="password" auto-complete="off" v-model="user.password">
                                <template slot="prepend">
                                <i class="el-icon-message"> </i>
                            </template>  
                        </el-input> 
                    </el-form-item> 
                    <el-form-item prop="confirmMemberpwd"> 
                        <el-input placeholder="" type="password" auto-complete="off" v-model="user.confirmMemberpwd">
                            <template slot="prepend">
                                <i class="el-icon-message">  </i>
                            </template> 
                        </el-input> 
                    </el-form-item> 
                        <!--<template>
                            <el-radio class="radio" v-model="radio" label="1"></el-radio>
                            <el-radio class="radio" v-model="radio" label="2"></el-radio>
                        </template>
                        -->
                        <!--/v1/captchas-->
                        <!--<el-input placeholder="" maxlength="4" v-model="codeNumber">
                            <template slot="append">
                                <img v-show="captchaCodeImg" :src="captchaCodeImg" />
                            </template>                    
                        </el-input>-->
                        <el-button type="primary" class="btn" @click="validate"></el-button>
                        <p><router-link to="/login"></router-link></p>
           </el-form>
        </div>
</template>
<script>
import axios from 'axios'
export default {
    data() {
        var validatePass2 = (rule,value,callback) => {
            if(value === '') {
                callback(new Error(''))
            }else if(value !== this.user.password) {
                callback(new Error(''))
            }else{
                callback()
            }
        }
        return {
            // :userrules
            user: {
                username: '',
                password: '',
                confirmMemberpwd:'',
                name:''
               
            },
            rules2: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                     
                ],
                name: [
                    { required: true, message: '', trigger: 'blur' },
                      
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                     
                ],
                confirmMemberpwd: [
                    { validator: validatePass2, trigger: 'blur' },
                    
                ]
            },
            confirmMemberpwd: '',
            radio:'1'
        }
    },
    computed: {
        // 
        rightPhoneNumber: function () {

            return /^1[34578]\d{9}$/gi.test(this.user.memberName)
        }
    },
    mounted() {
        // this.user.memberLvl = this.radio
    },
    methods: {
        validate() {
            
            this.$refs.ruleForm.validate((valid) => {
                if(valid) {
                     this.$store.dispatch('registerUser', this.user).then((res) => {
                        if (!res) {
                            alert('')
                        } else {
                            this.$router.push('/user/userList')
                        }
                    })
                }else {
                    console.log('error submit !')
                    return false
                }
            })
            
           

        }

    }

}
</script>
<style>
.form_box {
    width: 300px;
    margin: 0 auto;
    padding: 50px 0;
}

.form_box .el-input {
    margin: 10px 0;
}

.form_box .btn {
    width: 300px;
    margin-top: 20px;
}
</style>



